import Vue from 'vue';
import i18n from '../../../../i18n';
export default Vue.extend({
    i18n: i18n('desktop/views/components/settings.apps.vue'),
    data() {
        return {
            fetching: true,
            apps: []
        };
    },
    mounted() {
        this.$root.api('i/authorized_apps').then(apps => {
            this.apps = apps;
            this.fetching = false;
        });
    }
});
