import Vue from 'vue';
import i18n from '../../../../i18n';
import * as JSON5 from 'json5';
export default Vue.extend({
    i18n: i18n('common/views/components/api-settings.vue'),
    data() {
        return {
            endpoint: '',
            body: '{}',
            res: null,
            sending: false,
            endpoints: []
        };
    },
    created() {
        this.$root.api('endpoints').then(endpoints => {
            this.endpoints = endpoints;
        });
    },
    methods: {
        regenerateToken() {
            this.$root.dialog({
                title: this.$t('enter-password'),
                input: {
                    type: 'password'
                }
            }).then(({ canceled, result: password }) => {
                if (canceled)
                    return;
                this.$root.api('i/regenerate_token', {
                    password: password
                });
            });
        },
        send() {
            this.sending = true;
            this.$root.api(this.endpoint, JSON5.parse(this.body)).then(res => {
                this.sending = false;
                this.res = JSON5.stringify(res, null, 2);
            }, err => {
                this.sending = false;
                this.res = JSON5.stringify(err, null, 2);
            });
        },
        onEndpointChange() {
            this.$root.api('endpoint', { endpoint: this.endpoint }).then(endpoint => {
                const body = {};
                for (const p of endpoint.params) {
                    body[p.name] =
                        p.type === 'String' ? '' :
                            p.type === 'Number' ? 0 :
                                p.type === 'Boolean' ? false :
                                    p.type === 'Array' ? [] :
                                        p.type === 'Object' ? {} :
                                            null;
                }
                this.body = JSON5.stringify(body, null, 2);
            });
        }
    }
});
