import Vue from 'vue';
import i18n from '../../../../i18n';
import { apiUrl } from '../../../../config';
export default Vue.extend({
    i18n: i18n('common/views/components/integration-settings.vue'),
    data() {
        return {
            apiUrl,
            twitterForm: null,
            discordForm: null,
            githubForm: null,
            enableTwitterIntegration: false,
            enableDiscordIntegration: false,
            enableGithubIntegration: false,
        };
    },
    created() {
        this.$root.getMeta().then(meta => {
            this.enableTwitterIntegration = meta.enableTwitterIntegration;
            this.enableDiscordIntegration = meta.enableDiscordIntegration;
            this.enableGithubIntegration = meta.enableGithubIntegration;
        });
    },
    mounted() {
        if (!document.cookie.match(/i=(\w+)/)) {
            document.cookie = `i=${this.$store.state.i.token}; path=/;` +
                ` domain=${document.location.hostname}; max-age=31536000;` +
                (document.location.protocol.startsWith('https') ? ' secure' : '');
        }
        this.$watch('$store.state.i', () => {
            if (this.$store.state.i.twitter) {
                if (this.twitterForm)
                    this.twitterForm.close();
            }
            if (this.$store.state.i.discord) {
                if (this.discordForm)
                    this.discordForm.close();
            }
            if (this.$store.state.i.github) {
                if (this.githubForm)
                    this.githubForm.close();
            }
        }, {
            deep: true
        });
    },
    methods: {
        connectTwitter() {
            this.twitterForm = window.open(apiUrl + '/connect/twitter', 'twitter_connect_window', 'height=570, width=520');
        },
        disconnectTwitter() {
            window.open(apiUrl + '/disconnect/twitter', 'twitter_disconnect_window', 'height=570, width=520');
        },
        connectDiscord() {
            this.discordForm = window.open(apiUrl + '/connect/discord', 'discord_connect_window', 'height=570, width=520');
        },
        disconnectDiscord() {
            window.open(apiUrl + '/disconnect/discord', 'discord_disconnect_window', 'height=570, width=520');
        },
        connectGithub() {
            this.githubForm = window.open(apiUrl + '/connect/github', 'github_connect_window', 'height=570, width=520');
        },
        disconnectGithub() {
            window.open(apiUrl + '/disconnect/github', 'github_disconnect_window', 'height=570, width=520');
        },
    }
});
